.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.board_container {
  position: relative;
}

button {
  outline: none;
  margin: 5px;
  background: -webkit-gradient( linear, left bottom, left top, from(limegreen), to(lime));
  background: -webkit-linear-gradient( bottom, limegreen, lime);
  background: -o-linear-gradient( bottom, limegreen, lime);
  background: linear-gradient( to top, limegreen, lime); /* lawngreen, springgreen); */
  border-radius: 10px;
}
button:hover {
  -webkit-box-shadow: 0px 0px 10px yellow;
          box-shadow: 0px 0px 10px yellow;
}



